import React, {useState} from 'react'
import { BsArrowLeft, BsArrowRight } from "react-icons/bs";
import data from './data'

const Review = () => {
  const [value,setValue] = useState(0);
  const {name,img,desc,position} = data[value]

  const numberCorrection = (number)=>{
    if(number > data.length-1){
      return 0;
    }
    if(number < 0){
      return data.length-1;
    }
    return number;
  }

  const decrement = ()=>{
    setValue(value =>{
     let newValue = value - 1
     return numberCorrection(newValue);
    } )
  }

  const increment = ()=>{
    setValue(value =>{
     let newValue = value + 1
     return numberCorrection(newValue);
    } )
  }
 
  return (
    <section>
      <span className="btn btn-prev" onClick={decrement}>
      < BsArrowLeft />
      </span>
      <article>
        <div className="img">
        <img src={img} alt=""/>
        </div>
        <div className="info">
          <h3>{name}</h3>
          <div className="underline"></div>
          <h4>{position}</h4>
          <p>{desc}</p>
        </div>
      </article>
      <span className="btn btn-next" onClick={increment}>
      < BsArrowRight />
      </span>
    </section>
    )
}
 
export default Review;